import React from "react";
import { connect } from "react-redux";
import {
  BottomNavigation,
  AppBar,
  Toolbar,
  Typography,
  createMuiTheme,
  ThemeProvider,
  Slide,
  Button,
  BottomNavigationAction,
  TextField,
  IconButton,
  InputAdornment
} from "@material-ui/core";

import { Image, Visibility } from "semantic-ui-react";
import { Home, Search, NearMe, Whatshot, Info } from "@material-ui/icons";
import { orange } from "@material-ui/core/colors";
const myWhite = "rgba(255,255,255,0.9)";
const myBlack = "rgba(0,0,0,0.5)";
const myTheme = createMuiTheme({
  palette: {
    primary: {
      main: orange[900]
    },
    secondary: {
      main: myBlack
    }
  }
});

const bottomTheme = createMuiTheme({
  palette: {
    primary: {
      main: "rgba(0,0,0,1)"
    }
  }
});

const ForMobile = props => {
  const [isTop, setDirection] = React.useState(true);
  const [topVisible, setTop] = React.useState(true);
  return (
    <Visibility
      onUpdate={(e, { calculations }) => {
        setTop(calculations.topVisible);
        setDirection(calculations.direction === "up");
      }}
    >
      <ThemeProvider theme={myTheme}>
        <Slide in={isTop || topVisible}>
          <AppBar
            color="secondary"
            style={{ marginBottom: 10 }}
            position={topVisible ? "static" : "fixed"}
          >
            <Toolbar style={{ height: 10 }}>
              <Button
                style={{ textTransform: "none" }}
                onClick={() => {
                  props.dispatch({
                    type: "Home"
                  });
                  props.history.push("Home");
                }}
              >
                <Image
                  src={require("../../../images/complainify.png")}
                  size="mini"
                ></Image>
                <Typography
                  variant="h6"
                  style={{ color: "white", marginLeft: "10%" }}
                >
                  Anonymity
                </Typography>
              </Button>
              <div style={{ flexGrow: 1 }}></div>

              <IconButton>
                <Search style={{ color: "white" }}></Search>
              </IconButton>
            </Toolbar>
          </AppBar>
        </Slide>
      </ThemeProvider>
      <ThemeProvider theme={bottomTheme}>
        <BottomNavigation
          value={props.currentTab}
          onChange={(event, type) => {
            props.dispatch({
              type
            });
            props.history.push(type);
          }}
          style={{ position: "fixed", bottom: 0, zIndex: 100, width: "100%" }}
        >
          <BottomNavigationAction
            label="Home"
            value={"Home"}
            icon={<Home style={{ color: "orange" }}></Home>}
          ></BottomNavigationAction>
          <BottomNavigationAction
            label="NearBy"
            value={"Nearby"}
            icon={<NearMe style={{ color: "orange" }}></NearMe>}
          ></BottomNavigationAction>
          <BottomNavigationAction
            label="18+"
            value={"Adult"}
            icon={<Whatshot style={{ color: "orange" }}></Whatshot>}
          ></BottomNavigationAction>
          <BottomNavigationAction
            label="About"
            value={"About"}
            icon={<Info style={{ color: "orange" }}></Info>}
          ></BottomNavigationAction>
        </BottomNavigation>
      </ThemeProvider>
    </Visibility>
  );
};

const mapStateToProps = state => {
  return {
    currentTab: state.navigationReducer.currentTab
  };
};

export default connect(mapStateToProps)(ForMobile);
