import { Home, Adult, About, NearBy } from "./actionTypes";

export const setHome = () => {
  return {
    type: Home
  };
};

export const setAdult = () => {
  return {
    type: Adult
  };
};

export const setAbout = () => {
  return {
    type: About
  };
};

export const setNearBy = () => {
  return {
    type: NearBy
  };
};
