import React from "react";
import { connect } from "react-redux";

import { setAbout, setAdult, setHome, setNearBy } from "../../redux/action";

import HeaderComp from "./forComputers/computer";
import HeaderTab from "./forTablet/tablet";
import { Responsive } from "semantic-ui-react";
import HeaderMob from "./forMobiles/mobile";
class Home extends React.Component {
  constructor(props) {
    super(props);
  }
  componentDidMount() {
    const curr = this.props.location.pathname.replace("/anonymity/", "");
    console.log(curr);
    if (curr === "Home") this.props.setHome();
    if (curr === "Adult") this.props.setAdult();
    if (curr === "About") this.props.setAbout();
    if (curr === "Nearby") this.props.setNearBy();
  }
  render() {
    return (
      <div>
        <Responsive {...Responsive.onlyComputer}>
          <HeaderComp history={this.props.history}></HeaderComp>
        </Responsive>

        <Responsive {...Responsive.onlyTablet}>
          <HeaderTab history={this.props.history}></HeaderTab>
        </Responsive>
        <Responsive {...Responsive.onlyMobile}>
          <HeaderMob history={this.props.history}></HeaderMob>
        </Responsive>
      </div>
    );
  }
}

// const mapStateToProps = state => {
//   const { navigationReducer } = state;
//   return {
//     currentTab: navigationReducer.currentTab
//   };
// };

const mapStateToProps = state => {
  return {
    currentTab: state.navigationReducer.currentTab,
    width: state.visibility.Width
  };
};

export default connect(mapStateToProps, {
  setAbout,
  setAdult,
  setHome,
  setNearBy
})(Home);
