import React, { Component, Fragment, createRef } from "react";
import { setAbout, setAdult, setHome, setNearBy } from "../../../redux/action";
import { createMuiTheme, ThemeProvider } from "@material-ui/core/styles";
import { connect } from "react-redux";
import "./style.css";
import _ from "lodash";
import {
  Card,
  Icon,
  Image,
  Transition,
  Grid,
  Button,
  Modal,
  Loader,
  Dimmer,
  Responsive,
  Form,
  Sidebar,
  Menu,
  Sticky,
  Visibility,
  Header
} from "semantic-ui-react";
import Fab from "@material-ui/core/Fab";
import seducingImages from "../../../strings/SIURL";

import green from "@material-ui/core/colors/green";
import { Zoom, Button as MyButton } from "@material-ui/core";

const RandomPhotoGenerator = array => {
  let temp = array;
  let random = [];
  let randomNumber = Math.floor(Math.random() * array.length);
  for (let i = 0; i < array.length; i++) {
    random.push(temp[randomNumber]);
    temp = temp.filter((val, ind) => {
      return ind != randomNumber;
    });
  }
  return random;
};
const randomPhotos = RandomPhotoGenerator(seducingImages);
const floatingButtonTheme = createMuiTheme({
  palette: {
    secondary: {
      main: green[100]
    }
  }
});

class Home extends Component {
  constructor(props) {
    super(props);
    this.state = {
      visible: true,
      loading: false,
      showCharacterModal: false,
      showPasswordModal: false,
      modalImage: "",
      enteredPass: "",
      passCorrect: true,
      imagesToBeShown: randomPhotos.slice(0, 10),
      cantLoadMore: false,
      currentLength: 10,
      topVisible: true,
      showSidebarOpenButton: false,
      showSideBar: false,
      contextRef: ""
    };

    this.onClick = this.onClick.bind(this);
    this.onModalRequest = this.onModalRequest.bind(this);
    this.handleModalClose = this.handleModalClose.bind(this);
    this.onChangePass = this.onChangePass.bind(this);
    this.onPassClose = this.onPassClose.bind(this);
    this.onPassCheck = this.onPassCheck.bind(this);
    this.onLoadMore = this.onLoadMore.bind(this);

    this.handleUpdate = this.handleUpdate.bind(this);
    this.handleHide = this.handleHide.bind(this);
    this.handleOpenSide = this.handleOpenSide.bind(this);
    this.handleRef = this.handleRef.bind(this);
  }
  componentDidMount() {
    console.log(
      `Random Photos = ${randomPhotos.length} originalPhotos = ${seducingImages.length}`
    );
  }
  handleHide(e) {
    this.setState({ showSideBar: false });
  }
  handleOpenSide(e) {
    this.setState({ showSideBar: true });
  }
  onClick(e) {
    console.log(this.props.currentTab);
    this.setState(state => ({
      showPasswordModal: true,
      passCorrect: true
    }));
  }
  onModalRequest(e) {
    this.setState({ showCharacterModal: true, modalImage: `${e.target.src}` });
  }
  handleModalClose() {
    this.setState({ showCharacterModal: false, modalImage: `` });
  }
  onChangePass(e) {
    this.setState({ [e.target.name]: e.target.value, passCorrect: true });
  }
  onPassClose(e) {
    this.setState({ showPasswordModal: false });
  }

  onPassCheck(e) {
    let password = "iwonttellyou";
    console.log(e.target.value);
    this.setState(state => ({
      passCorrect: password === state.enteredPass,
      showPasswordModal: !(password === state.enteredPass),
      loading: password === state.enteredPass
    }));
    setTimeout(() => {
      this.setState(state => ({ visible: state.passCorrect, loading: false }));
    }, 1000);
  }
  handleRef(e) {
    console.log(e);
    this.setState({ contextRef: e });
  }
  onLoadMore(e) {
    console.log("hello");

    if (seducingImages.length - this.state.currentLength > 5) {
      return this.setState(state => ({
        imagesToBeShown: state.imagesToBeShown.concat([
          randomPhotos[state.currentLength + 1],
          randomPhotos[state.currentLength + 2],
          randomPhotos[state.currentLength + 3],
          randomPhotos[state.currentLength + 4],
          randomPhotos[state.currentLength + 5]
        ]),
        currentLength: state.currentLength + 5,
        cantLoadMore: state.currentLength >= seducingImages.length
      }));
    }
    return this.setState({ cantLoadMore: true });
  }
  handleUpdate(e, { calculations }) {
    this.setState(state => ({
      topVisible: calculations.topVisible,
      showSidebarOpenButton:
        calculations.width < 1756 && !calculations.topVisible
    }));
  }
  render() {
    let { contextRef } = this.state;
    let imagesCard = this.state.imagesToBeShown.map(url => (
      <Card
        key={url}
        style={{
          border: "none",

          boxShadow: "0px 10px 10px lightgrey",
          width: "100%"
        }}
      >
        <div style={{ overflow: "hidden" }}>
          <div onClick={this.onModalRequest} className="reblur">
            <Image src={url} centered></Image>
          </div>
        </div>
        <Card.Content>
          <Card.Header>Dead Or Alive 6</Card.Header>
          <Card.Meta>Sensitive Content Click To View It Any Way</Card.Meta>
        </Card.Content>
      </Card>
    ));

    let cardsGroup = this.state.loading ? (
      ""
    ) : (
      <Card.Group centered itemsPerRow="1">
        {imagesCard}
      </Card.Group>
    );

    return (
      <div>
        <Visibility onUpdate={this.handleUpdate}></Visibility>

        <Modal
          open={this.state.showPasswordModal}
          basic
          centered
          onClose={this.handlePasswordModalClose}
        >
          <Transition visible={this.state.showPasswordModal}>
            <Form style={{ paddingRight: "10%", paddingLeft: "10%" }}>
              <Form.Field>
                <Transition
                  visible={this.state.passCorrect}
                  animation={`${!this.state.passCorrect ? "shake" : ""}`}
                  duration={500}
                >
                  <input
                    value={this.state.enteredPass}
                    name="enteredPass"
                    onChange={this.onChangePass}
                    style={{
                      backgroundColor: "transparent",
                      border: `1px solid ${
                        this.state.passCorrect ? "grey" : "red"
                      }`,
                      color: "white"
                    }}
                    placeholder="Enter Password"
                    type="password"
                  ></input>
                </Transition>
              </Form.Field>
              <Button color="green" onClick={this.onPassCheck} inverted>
                Enter
              </Button>
              <Button color="red" onClick={this.onPassClose} inverted>
                Cancel
              </Button>
            </Form>
          </Transition>
        </Modal>
        <Transition visible={this.state.showCharacterModal} duration={500}>
          <Modal
            open={this.state.showCharacterModal}
            onClose={this.handleModalClose}
            basic
            size="large"
            dimmer="blurring"
          >
            <Modal.Content image>
              <Image
                wrapped
                centered
                as="a"
                target="_blank"
                href={this.state.modalImage}
                src={this.state.modalImage}
              />
            </Modal.Content>

            <Modal.Actions>
              <Button color="red" inverted onClick={this.handleModalClose}>
                Close
              </Button>
            </Modal.Actions>
          </Modal>
        </Transition>
        <Dimmer active={this.state.loading}>
          <Loader></Loader>
        </Dimmer>
        <Sidebar.Pushable>
          {this.state.visible ? (
            ""
          ) : (
            <div
              style={{
                display: "flex",
                justifyContent: "center",
                marginTop: 100
              }}
            >
              <MyButton
                variant="outlined"
                color="secondary"
                onClick={this.onClick}
              >
                Enter The Heaven
              </MyButton>
            </div>
          )}
        </Sidebar.Pushable>

        <Responsive style={{ border: "none" }} {...Responsive.onlyComputer}>
          {this.state.visible ? (
            <div ref={this.handleRef}>
              <Grid centered divided>
                <Grid.Column width="3">
                  <Sticky context={contextRef} offset={10}>
                    <Transition
                      visible={
                        !this.state.topVisible &&
                        !this.state.showSidebarOpenButton
                      }
                      animation="fade down"
                      duration={300}
                    >
                      <Menu
                        style={{
                          marginLeft: "5%"
                        }}
                        vertical
                        size="huge"
                      >
                        <Menu.Item header>
                          <div
                            style={{
                              fontFamily: "poppins",
                              fontWeight: "bold"
                            }}
                          >
                            <Image
                              src={require("../../../images/complainify.png")}
                              size="tiny"
                              rounded
                            ></Image>
                            Anonimity
                          </div>
                        </Menu.Item>

                        <Menu.Item
                          as="a"
                          onClick={() => {
                            this.props.history.push("Home");
                            this.props.setHome();
                          }}
                        >
                          Home
                        </Menu.Item>
                        <Menu.Item
                          as="a"
                          onClick={() => {
                            this.props.history.push("NearBy");
                            this.props.setNearBy();
                          }}
                        >
                          NearBy
                        </Menu.Item>
                        <Menu.Item
                          as="a"
                          onClick={() => {
                            this.props.history.push("About");
                            this.props.setAbout();
                          }}
                        >
                          About
                        </Menu.Item>
                        <Menu.Item as="a" active>
                          Adult
                        </Menu.Item>
                      </Menu>
                    </Transition>
                  </Sticky>
                </Grid.Column>

                <Grid.Column width="7">
                  {this.state.visible ? cardsGroup : ""}
                </Grid.Column>
                <Grid.Column width="3"></Grid.Column>
              </Grid>
            </div>
          ) : (
            ""
          )}
        </Responsive>

        <Responsive {...Responsive.onlyTablet}>
          {this.state.visible ? (
            <div ref={this.handleRef}>
              <Grid centered divided>
                <Grid.Column width="1"></Grid.Column>
                <Grid.Column width="14">
                  {this.state.visible ? cardsGroup : ""}
                </Grid.Column>
                <Grid.Column width="1"></Grid.Column>
              </Grid>
            </div>
          ) : (
            ""
          )}
        </Responsive>
        <Responsive {...Responsive.onlyMobile}>
          <Grid>
            <Grid.Column>{this.state.visible ? cardsGroup : ""}</Grid.Column>
          </Grid>
        </Responsive>
        {this.state.visible ? (
          <div
            style={{ display: "flex", justifyContent: "center", margin: 10 }}
          >
            <MyButton
              disabled={this.state.cantLoadMore}
              onClick={this.onLoadMore}
            >
              {this.state.cantLoadMore ? "Can't Load More" : "Load More"}
            </MyButton>
          </div>
        ) : (
          ""
        )}
      </div>
    );
  }
}

// {this.state.visible ? cardsGroup : ""}

