//     <Grid.Column width="10">
//       {this.state.visible ? cardsGroup : ""}
//     </Grid.Column>
//     <Grid.Column width="3"></Grid.Column>
//   </Grid>
// </Fragment>

const mapStateToProps = state => {
  return {
    currentTab: state.navigationReducer.currentTab
  };
};

export default connect(mapStateToProps, {
  setAbout,
  setAdult,
  setHome,
  setNearBy
})(Home);
