import React from "react";
import { connect } from "react-redux";
import {
  AppBar,
  createMuiTheme,
  ThemeProvider,
  Typography,
  Toolbar,
  Button,
  IconButton,
  Menu,
  MenuItem
} from "@material-ui/core";
import { Divider, Search } from "semantic-ui-react";
import "../styles/lift.css";
import { setAbout, setAdult, setHome, setNearBy } from "../../../redux/action";
import { Image } from "semantic-ui-react";
import { orange } from "@material-ui/core/colors";
import { Adult, NearBy, Home as home, About } from "../../../redux/actionTypes";
import "./style.css";
import {
  OutdoorGrill,
  VerifiedUser,
  SupervisedUserCircle,
  Person
} from "@material-ui/icons";
const myOrange = "rgba(255,165,0,1)";

const theme1 = createMuiTheme({
  palette: {
    secondary: {
      main: "rgba(255,255,255,1)"
    },
    primary: orange
  }
});

const ForComputer = props => {
  return (
    <ThemeProvider theme={theme1}>
      <AppBar color="secondary" elevation={0} position="sticky">
        <Toolbar
          style={{
            marginLeft: "8%",
            marginRight: "1%"
          }}
        >
          <Image
            src={require("../../../images/complainify.png")}
            size="tiny"
            style={{ marginRight: 5 }}
          ></Image>
          <Typography
            style={{
              color: "rgba(0,0,0,0.6)",
              float: "left",
              fontWeight: "bold",
              marginRight: "5%"
              // flexGrow: 1
            }}
            variant="h6"
          >
            Anonymity
          </Typography>

          <Button
            size="large"
            onClick={() => {
              props.setHome();
              props.history.push(home);
            }}
            style={{
              marginRight: 10,
              textTransform: "none",
              fontFamily: "poppins",

              color: "black",
              backgroundColor: props.currentTab === home ? myOrange : ""
            }}
          >
            Home
          </Button>
          <Button
            disableElevation
            onClick={() => {
              props.setNearBy();
              props.history.push(NearBy);
            }}
            size="large"
            style={{
              marginRight: 10,
              textTransform: "none",
              fontFamily: "poppins",

              backgroundColor: props.currentTab === NearBy ? myOrange : ""
            }}
          >
            Near By
          </Button>
          <Button
            size="large"
            style={{
              textTransform: "none",
              fontFamily: "poppins",
              marginRight: 10,
              backgroundColor: props.currentTab === Adult ? myOrange : ""
            }}
            onClick={() => {
              props.setAdult();
              props.history.push(Adult);
            }}
          >
            Adult
          </Button>
          <Button
            size="large"
            onClick={() => {
              props.setAbout();
              props.history.push(About);
            }}
            style={{
              marginRight: 10,
              textTransform: "none",
              fontFamily: "poppins",

              backgroundColor: props.currentTab === About ? myOrange : ""
            }}
          >
            About
          </Button>
          <div style={{ flexGrow: 1 }}></div>
          {props.currentTab != "Adult" ? <Search loading={false}></Search> : ""}
          <Button
            size="large"
            style={{
              margin: 10,
              color: "grey",
              fontWeight: "bold",
              textTransform: "none",
              fontFamily: "poppins"
            }}
          >
            <Person style={{ marginRight: 10, color: "orange" }}></Person>
            ParasKanwar30
          </Button>
        </Toolbar>
      </AppBar>
      <Divider style={{ marginTop: 0 }}></Divider>
    </ThemeProvider>
  );
};

// const mapStateToProps = state => {
//   const { navigationReducer } = state;
//   return {
//     currentTab: navigationReducer.currentTab
//   };
// };

const mapStateToProps = state => {
  return {
    currentTab: state.navigationReducer.currentTab,
    width: state.visibility.Width
  };
};

export default connect(mapStateToProps, {
  setAbout,
  setAdult,
  setHome,
  setNearBy
})(ForComputer);
